@import '../variables/colors';
@import '../variables/font';
@import '../abstracts/invertColor';
@import '../elements/input';
@import '../elements/button';
.CitySearch {
  display:flex;
  justify-content: space-between;
  color: $color-lighter;
  flex-direction: column;
  @include respond-to(lgr-screen){
    flex-direction: row;
  }
  // margin: 3em auto;
  input {
    @extend input-element;
    margin-bottom: 1em;
  }
  &__btn {
    @extend button-element;
}
}
.suggestions {
  li{
    list-style: none;
  }
}