@import '../variables/colors';
@import '../variables/font';
@import '../abstracts/mediaQueries';
@import '../elements/input';
@import '../elements/button';
.NumberOfEvents{
  @include respond-to(md-screen){
    display:flex;
    justify-content: space-between;
    margin-bottom: 5em;
  }
  p {
    color: $color-light;
    margin-bottom:.4em;
    @include respond-to(md-screen){
      font-size:$font-3;
    }
  }
  input {
    @extend input-element;
    
  }
}