@import '../abstracts/mediaQueries';
@import '../variables/colors';
@import '../variables/font';
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  background-color: $color-main;
  
  // navbar & footer
  .navbar, .footer {
    p {
      font-size: $font-3;
    }
  }
  .navbar{
    color: $color-lighter;
  }
  .footer {
    color: $color-dark;
  }
  
  // main
  .main__container{
    font-size: $font-2;
    display: flex;
    // place-content: center;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    margin: 5em 0;
  }
  .input__container{
    display:flex;
    width: 50%;
    flex-direction: column;
    @include respond-to(lg-screen){
      display:flex;
      align-items: center;
      min-height: 20vh;
      width: 40vw;
      align-items: normal;
      flex-direction: column;
      justify-content: space-between;
      margin-bottom: 2em;
    }
  }
}


